

<template>
  <!--  -->
  <div class="home">
    <MainTop>  </MainTop>

    <div class="home-bottom padding15">
      <div id="containerhome"></div>
      <div class="block">
        <el-carousel height="350px" :interval="5000">
          <el-carousel-item>
            <img
              class="img1"
              src="https://vkceyugu.cdn.bspapp.com/VKCEYUGU-790d5b85-9674-4a89-9bcc-c0657ea369be/25239b78-3d66-4137-bf12-37f954432a42.png"
              alt=""
            />
          </el-carousel-item>
          <el-carousel-item>
            <img
              class="img2"
              src="https://vkceyugu.cdn.bspapp.com/VKCEYUGU-790d5b85-9674-4a89-9bcc-c0657ea369be/f0bc2a5c-46b3-4d3e-9f21-1d17b74a9d0b.png"
              alt=""
            />
          </el-carousel-item>
          <el-carousel-item>
            <img
              class="img3"
              src="https://vkceyugu.cdn.bspapp.com/VKCEYUGU-790d5b85-9674-4a89-9bcc-c0657ea369be/f4daba88-6815-4353-96b5-cd63d8069d63.png"
              alt=""
            />
          </el-carousel-item>
          <el-carousel-item>
            <img
              class="img4"
              src="https://vkceyugu.cdn.bspapp.com/VKCEYUGU-790d5b85-9674-4a89-9bcc-c0657ea369be/ce4c4adb-16c8-413e-a940-b710b161e8ee.png"
              alt=""
            />
          </el-carousel-item>
          <el-carousel-item>
            <img
              class="img5"
              src="https://vkceyugu.cdn.bspapp.com/VKCEYUGU-790d5b85-9674-4a89-9bcc-c0657ea369be/ba83ae49-e379-4cfa-908d-d25f4924ee35.png"
              alt=""
            />
          </el-carousel-item>
          <!-- <el-carousel-item v-for="item in 4" :key="item">
            <h3 class="small">{{ item }}</h3>
          </el-carousel-item> -->
        </el-carousel>
      </div>
      <div class="home-bottom-usermain">
        <div class="usermain-left">
          <el-card class="box-card" style="height: 320px">
            <span style="font-size: 16px; font-weight: 600"></span>
            <div class="notic-list">
              <div
                class="notic-che"
                @click="getNoticMain(ele)"
                v-for="ele in noticeList"
                :key="ele._id"
              >
                <span>[{{ ele.region }}] {{ ele.name }}</span
                ><span>{{ ele.date1 }}</span>
              </div>
            </div>
          </el-card>
        </div>
        <div class="usermain-right">
          <el-card class="box-card" style="height: 320px">
            <div class="user">
              <span>{{ nowUser }}</span>
              <el-tag v-if="nowUser === 'admin'"></el-tag>
              <el-tag v-else type="info"></el-tag>
            </div>
            <div class="address">
              <span></span>
              <el-tag v-if="geolocationRes.info !== 'SUCCESS'" type="danger"
                ></el-tag
              >
              <el-tag v-else type="success"></el-tag>
            </div>
            <div class="address">
              <span></span>
              <el-tag
                v-if="geolocationRes.location_type === 'ip'"
                effect="plain"
                type="info"
                ></el-tag
              >
              <el-tag v-else effect="plain" type="info"></el-tag>
            </div>
            <div class="address">
              <span class="miniText">{{
                geolocationRes.formattedAddress
              }}</span>
            </div>

            <!--  -->
            <div class="weather" v-if="todayWeather">
              <div class="address">
                <span class="miniText">{{ todayWeather.reportTime }}</span>
                <span class="miniText">{{ todayWeather.city }}</span>
              </div>
              <div class="address">
                <span class="miniText"
                  > {{ todayWeather.temperature }}</span
                >
                <span class="miniText" v-if="todayWeather.temperature < 5"
                  ></span
                >
                <span
                  class="miniText"
                  v-if="
                    todayWeather.temperature >= 5 &&
                    todayWeather.temperature < 15
                  "
                  ></span
                >
                <span class="miniText" v-if="todayWeather.temperature >= 15"
                  ></span
                >
              </div>
              <div class="address">
                <span class="miniText"
                  > {{ todayWeather.windDirection }}</span
                >
                <span class="miniText"> {{ todayWeather.weather }}</span>
              </div>
            </div>

            <div class="weather" v-else>
              <div class="address">
                <span class="miniText"></span>
              </div>
            </div>
          </el-card>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript"
    src="https://webapi.amap.com/maps?v=1.4.15&key=2eccb47b400c8ab58f2dc596dbfe9d53&plugin=AMap.MouseTool,AMap.Driving,AMap.DistrictSearch,AMap.Geocoder"></script>
<script>
import MainTop from "../components/MainTop.vue";
export default {
  components: { MainTop },

  data() {
    return {
      map: "",
      geolocationRes: {}, //
      dialogVisible: false, //
      geolocation: null,
      nowUser: "",
      todayWeather: null,
      fourDayWeatherList: null,
      weatherPlace: null,

      noticeList: [], //
    };
  },

  created() {
    this.initMap();
    this.nowUser = sessionStorage.getItem("username");
    this.getNoticeList();
  },

  methods: {
    // 
    initMap() {
      //
      this.map = new AMap.Map("containerhome", {
        resizeEnable: true,
        // zoom: this.zoom, //
        zooms: [6.5, 18],
        // center: [115.464523, 38.874476], //
        viewMode: "3D", //3D

        mapStyle: "amap://styles/macaron",
        // infoWindow: null,
      });

      AMap.plugin("AMap.Geolocation", () => {
        // console.log(111);
        // 
        this.geolocation = new AMap.Geolocation({
          enableHighAccuracy: true, //:true
          timeout: 10000, //105s
          buttonPosition: "RB", //
          buttonOffset: new AMap.Pixel(10, 20), //Pixel(10, 20)
          zoomToAccuracy: true, //
        });
        this.map.addControl(this.geolocation);
        this.geolocation.getCurrentPosition((status, result) => {
          if (status == "complete") {
            // onComplete(result);
            // console.log("", result);
            this.geolocationRes = result;
            console.log(this.geolocationRes);
            var start = this.geolocationRes.formattedAddress.search("");
            var end = this.geolocationRes.formattedAddress.search("");

            console.log("start", start, end);

            this.weatherPlace = this.geolocationRes.formattedAddress.slice(
              start + 1,
              end
            );

            // start

            console.log("", this.weatherPlace);
            AMap.plugin("AMap.Weather", () => {
              var weather = new AMap.Weather();

              //, 
              weather.getLive(this.weatherPlace, (err, data) => {
                if (!err) {
                  console.log("", data);
                  this.todayWeather = data;
                }
              });
              //4
              weather.getForecast(this.weatherPlace, (err, data) => {
                if (err) {
                  return;
                }
                console.log("4", data);
                this.fourDayWeatherList = data.forecasts;
                // console.log("", this.fourDayWeatherList);
              });
            });
            // end
          } else {
            // onError(result);
            this.$message.error(
              "Eage"
            );
            console.log("", result);
          }
        });
      });

      // start
    },

    async getNoticeList() {
      var res = await this.$axios.get("/getNoticeList/getNoticeList");
      console.log(res);
      this.noticeList = res.data.data;
    },
    getNoticMain(ele) {
      // console.log(ele);
      this.$router.push({
        path: "/main/noticemain?_id=" + ele._id,
      });
    },
  },
};
</script>

